'use client';

import { useState } from 'react';
import { RouteConfig, RequestBody, ApiResponse } from '@/types/route';

interface RouteTestProps {
  route: RouteConfig;
}

export default function RouteTest({ route }: RouteTestProps) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [requestBody, setRequestBody] = useState('{}');
  const [response, setResponse] = useState<ApiResponse | null>(null);
  const [loading, setLoading] = useState(false);

  const handleTest = async () => {
    setLoading(true);
    try {
      let url = `${process.env.NEXT_PUBLIC_API_URL}${route.path}`;
      
      // Parse request body
      let body: RequestBody = {};
      try {
        body = JSON.parse(requestBody);
      } catch (e) {
        setResponse({
          status: 400,
          data: null,
          error: 'Invalid JSON in request body'
        });
        return;
      }

      const response = await fetch(url, {
        method: route.method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: route.method !== 'GET' ? JSON.stringify(body) : undefined,
      });

      const data = await response.json();
      
      setResponse({
        status: response.status,
        data,
      });
    } catch (error) {
      setResponse({
        status: 500,
        data: null,
        error: error instanceof Error ? error.message : 'Unknown error occurred'
      });
    } finally {
      setLoading(false);
    }
  };

  const getMethodColor = (method: string) => {
    const colors = {
      GET: 'bg-blue-500',
      POST: 'bg-green-500',
      PUT: 'bg-yellow-500',
      DELETE: 'bg-red-500'
    };
    return colors[method as keyof typeof colors] || 'bg-gray-500';
  };

  return (
    <div className="border rounded-lg overflow-hidden">
      <div className="flex items-center p-4 bg-gray-50">
        <span className={`${getMethodColor(route.method)} text-white px-3 py-1 rounded-md mr-3 text-sm font-medium`}>
          {route.method}
        </span>
        <span className="font-mono text-sm">{route.path}</span>
        <button
          className="ml-auto text-gray-500 hover:text-gray-700"
          onClick={() => setIsExpanded(!isExpanded)}
        >
          {isExpanded ? '▼' : '▶'}
        </button>
      </div>

      {isExpanded && (
        <div className="p-4 border-t">
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Request Body
            </label>
            <textarea
              className="w-full h-32 p-2 border rounded-md font-mono text-sm"
              value={requestBody}
              onChange={(e) => setRequestBody(e.target.value)}
            />
          </div>

          <button
            className="bg-indigo-500 text-white px-4 py-2 rounded-md hover:bg-indigo-600 disabled:opacity-50"
            onClick={handleTest}
            disabled={loading}
          >
            {loading ? 'Testing...' : 'Test Endpoint'}
          </button>

          {response && (
            <div className="mt-4">
              <h4 className="font-medium text-gray-700 mb-2">Response</h4>
              <div className="bg-gray-100 p-4 rounded-md">
                <div className="mb-2">
                  Status: <span className={response.status >= 400 ? 'text-red-500' : 'text-green-500'}>
                    {response.status}
                  </span>
                </div>
                <pre className="whitespace-pre-wrap font-mono text-sm">
                  {JSON.stringify(response.data, null, 2)}
                </pre>
                {response.error && (
                  <div className="text-red-500 mt-2">{response.error}</div>
                )}
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
} 